<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-button type="primary" @click="addDialogVisible = true">添加角色</el-button>
      <!-- 表格 -->
      <el-table :data="rolelist" stripe border>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <!-- 展开列下的template，即是展开区域的内容 -->
          <template v-slot="scope">
            <el-row
              :class="['vcenter', 'bdbottom', i1 == 0 ? 'bdtop' : '']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!-- 一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id)">{{ item1.authName }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二三级 -->
              <el-col :span="18">
                <el-row
                  :class="['vcenter', i2 == 0 ? '' : 'bdtop']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <!-- 二级权限 -->
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">
                      {{ item2.authName }}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级权限 -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                    >
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template v-slot="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)">
              编辑
            </el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeRoleById(scope.row.id)">
              删除
            </el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRightDialog(scope.row)"
            >
              分配角色
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加角色对话框 -->
    <el-dialog title="添加角色" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
      <el-form :model="addForm" :rules="FormRules" ref="addFormRef" width="70px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑角色对话框 -->
    <el-dialog title="编辑角色" :visible.sync="editDialogVisible" width="50%" @close="editDialogClose">
      <el-form :model="editForm" :rules="FormRules" ref="editFormRef" width="70px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRole">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配权限对话框 -->
    <el-dialog title="分配角色" :visible.sync="setRightDialogVisible" width="50%">
      <!-- 树形控件 -->
      <el-tree
        :data="rightslist"
        :props="treeProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getRolesList,
  addRole,
  getRoleById,
  editRole,
  removeRoleById,
  removeRightById,
  getRightsList,
  allotRights
} from '@/api/api.js'
export default {
  data() {
    return {
      // 角色列表
      rolelist: [],
      // 控制添加角色对话框的显示隐藏
      addDialogVisible: false,
      // 添加角色的表单数据（请求参数）
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      // 控制编辑角色对话框的显示隐藏
      editDialogVisible: false,
      // 编辑角色表单数据
      editForm: {},
      // 控制分配权限对话框的显示隐藏
      setRightDialogVisible: false,
      // 权限列表
      rightslist: [],
      // 树形控件的配置对象
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // 树形控件默认选中的节点id值数组
      defKeys: [],
      // 角色id，角色授权的请求参数
      roleId: '',
      // 表单的验证规则
      FormRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 2, max: 10, message: '角色名称长度3 ~ 10', trigger: 'blur' }
        ],
        roleDesc: []
      }
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // 获取角色列表数据
    async getRolesList() {
      const { data: res } = await getRolesList()
      if (res.meta.status !== 200) return this.$message.error('获取角色列表失败')
      this.rolelist = res.data
    },
    // 关闭添加角色对话框重置表单
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    // 点击确认，添加角色
    addRole() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await addRole(this.addForm)
        if (res.meta.status !== 201) return this.$message.error('添加角色失败')
        this.$message.success('添加角色成功')
        this.addDialogVisible = false
        this.getRolesList()
      })
    },
    // 点击编辑，显示修改角色对话框，展示角色信息供修改
    async showEditDialog(id) {
      console.log(id)
      const { data: res } = await getRoleById(id)
      if (res.meta.status !== 200) return this.$message.error('查询角色信息失败')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 关闭编辑角色对话框重置表单
    editDialogClose() {
      this.$refs.editFormRef.resetFields()
    },
    // 点击确认，修改角色信息
    editRole() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await editRole(this.editForm.roleId, {
          roleName: this.editForm.roleName,
          roleDesc: this.editForm.roleDesc
        })
        if (res.meta.status !== 200) return this.$message.error('更新角色信息失败')
        this.$message.success('更新角色信息成功')
        this.getRolesList()
        this.editDialogVisible = false
      })
    },
    // 删除用户
    async removeRoleById(id) {
      const confirmResult = await this.$confirm('是否删除该角色', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return
      const { data: res } = await removeRoleById(id)
      if (res.meta.status !== 200) return this.$message.error('删除角色失败')
      this.$message.success('删除角色成功')
      this.getRolesList()
    },
    // 根据id删除权限.
    async removeRightById(role, rightId) {
      const confirmResult = await this.$confirm('是否删除该权限', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return
      const { data: res } = await removeRightById(role.id, rightId)
      if (res.meta.status !== 200) return this.$message.error('删除权限失败')
      // 重写获取数据会刷新页面，导致权限区域折叠
      // this.getRolesList()
      // role是scope.row，res.data返回的是当下角色最新的权限
      // ！！！重点：为什么能对页面的scope.row进行赋值？
      // 因为scope.row是对象，引用传递。而scope.row也是this.rolelist某个属性的引用，所以本质上就是赋值给了this.rolelist的某个属性
      // ！！！关键就是参数传递的是 '引用类型'
      role.children = res.data
    },
    // 展示分配权限对话框
    async showSetRightDialog(role) {
      this.roleId = role.id
      const { data: res } = await getRightsList('tree')
      if (res.meta.status !== 200) return this.$message.error('获取权限数据失败')
      this.rightslist = res.data
      // 调用递归获取默认选中项的id
      this.defKeys = [] //因为递归中用的是push赋值，数据不会被覆盖，所以要先清空（教程是监听对话框关闭事件请求）
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // 递归获取角色的所有三级权限id，并保存到defKeys（树形控件默认选中的数组）
    getLeafKeys(node, arr) {
      // 如果该节点没有children，即是三级节点，将id存入arr
      if (!node.children) return arr.push(node.id)
      // 有children，调用该函数，并将children下的子节点作为参数传入
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 点击确认，为角色授权
    async allotRights() {
      // 被选中的叶子节点的id 和 半选节点的id
      const keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
      const idStr = keys.join(',')
      const { data: res } = await allotRights(this.roleId, idStr)
      if (res.meta.status !== 200) return this.$message.error('分配权限失败')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
